body {
  margin: 0;
  font-family: "Inter", system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #E7F6FF;
}

button, input{
  font-family: "Inter", system-ui;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper{
  max-width: 1080px;
  background: #FFFFFF;
box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
border-radius: 20px;
margin: 50px auto;
}



header{
  display: flex;
  justify-content: space-between;
  padding: 40px;
  border-bottom: 1px solid #EAEAEA;

  a{
    text-decoration: none;
    color: #000;
  }
}

.headerLeft{
  display: flex;
  align-items: center;
  img{
    width: 40px;
    height: 40px;
  }
  h3, p{
    margin: 0;
    padding-left: 10px;
  }
  h3{
    text-transform: uppercase;
  }
  p{
    opacity: 0.5;
  }
}

ul{
   padding-left: 0;
   margin-left: 0;
   display: flex;

  li{
    list-style-type: none;
  }
}

.headerRight{
  img{
    margin-right: 10px;
  }
  li{
    cursor: pointer;
  }
  li:first-child{
    margin-right: 10px;
  }
}

.content{
  padding: 40px;
  

  h1{
    margin: 0;

  }
}

.topContent{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;

  .backButton{
    display: flex;
    flex-direction: row;
    img{
      margin-right: 20px;
      cursor: pointer;
      transition: transform 0.2s ease-in-out;
    }

    img:hover{
      transform: translateX(-2px);
    }
  }
}

.sneakers{
display: flex;
flex-wrap: wrap;
}

.cardBottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cardPrice{
  display: flex;
  flex-direction: column;
}

.search-block{
  position: relative;
  border: 1px solid #F3F3F3;
  border-radius: 10px;
  padding: 0 15px;
  input{
    border: none;
    padding: 10px;
    font-style: 16px;
    width: 200px;
    box-sizing: border-box;
    outline: none;
  }
}

.clearBtn{
  position: absolute;
  right: 15px;
  top: 10px;
  width: 18px;
  height: 18px;

  cursor: pointer;
}

.cardEmpty{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex: 1;
  text-align: center;
  .emptyCardImg{
    margin-bottom: 20;
  }
  p{
    opacity: 0.6;
    width: 280px;
    line-height: 24px;
  }
  .greenButton{
    width: 245px;
    margin-top: 20px;

    &:hover{
      img{
        transform: rotate(180deg) translateX(3px);
      }
    }

    img{
      position: relative;
      top: 1px;
      transform: rotate(180deg);
      margin-right: 15px;
      transition: transform 0.15s ease-in-out;
    }
  }
}


.items{
  flex: 1;
  overflow: auto;
  margin-bottom: 40px;
}

.cartItem{
  display: flex;
  align-content: center;

  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;
  margin-bottom: 20px;

  .cartItemImg{
    height: 70px;
    width: 70px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 0 -3px;
    margin-right: 20px;
  }
  .cartItemInfo{
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-right: 20px;
  }

  p{
    font-size: 16px;
    margin: 0;
  }
  b{
    font-size: 14px;
  }
  img:first-child{
    margin-right: 20px;
  }



  .removeBtn{
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.15s ease-in-out;

    &:hover{
      opacity: 1;
    }
  }

}

.cartTotalBlock{

  ul{
    margin-bottom: 20px;
    flex-direction: column;
    li{
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;
    }
    div{
      flex: 1;
      height: 1px;
      border-bottom: 1px dashed #dfdfdf;
      position: relative;
      top: -4px;
      margin: 0 7px;
    }
  }

  .greenButton{
    position: relative;

    &:disabled{
      background-color: #bebebe;
      cursor: default;

      animation: button-loading 1s ease-in-out infinite;
    }

    &:hover{
      img{
          transform: translateX(5px);
      }
    }
   
    img{
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

@keyframes button-loading {
  0%{
    opacity: 1;
  }

  50%{
    opacity: 0.8;
  }

  100%{
    opacity: 1;
  }
}


.greenButton{
  width: 100%;
  height: 55px;
  background: #9DD558;
  border: 0;
  border-radius: 18px;

  color: #fff;
  font-size: 16px;
  font-weight: 600;
  transition: background 0.2s ease-in-out;
  cursor: pointer;

  &:hover{
    background: lighten(#9DD558, 5%);
  }

  &:active{
    background: darken(#9DD558, 5%);
  }
}

.emptyPage{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 400px;
  margin-bottom: 20px;

  h1{
    margin-top: 10px;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  }

  p{
    font-weight: 400;
    font-size: 16px;
    text-align: center;
    opacity: .5;
  }

  .greenButton{
    width: 260px;
    margin-top: 10px;
    &:hover{
      img{
        transform: rotate(180deg) translateX(3px);
      }
    }

    img{
      position: relative;
      top: 1px;
      transform: rotate(180deg);
      margin-right: 15px;
      transition: transform 0.15s ease-in-out;
    }

  }





}

